body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: Arial, sans-serif;
  background-color: black;
}

#bg-video {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}

.top-bar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 50px;
  background-color: rgba(200, 200, 200, 0.2);
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 30px;
  gap: 20px;
  z-index: 1000;
  backdrop-filter: blur(6px);
}

.top-bar a {
  color: black;
  text-decoration: none;
  font-size: 14px;
  font-weight: bold;
  padding: 6px 12px;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, 0.3);
  transition: background-color 0.2s ease-in-out;
}

.top-bar a:hover {
  background-color: rgba(255, 255, 255, 0.5);
}

.content {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.block {
  background-color: rgba(0, 0, 0, 0.6);
  padding: 40px;
  border-radius: 16px;
  color: white;
  text-align: center;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
  width: 400px;
  margin-top: 60px;
}
